import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';

import { Toast } from '@ionic-native/toast/ngx';
import { BarcodeScanner } from '@ionic-native/barcode-scanner/ngx';

import { ItemsSandbox } from '@sandboxes/items.sandbox';
import { IItem } from '@store/models/items.model';

const scannerOptions = {
  preferFrontCamera : true, // iOS and Android
  showFlipCameraButton : true, // iOS and Android
  showTorchButton : true, // iOS and Android
  disableSuccessBeep: false // iOS and Android
};

@Component({
  selector: 'page-edit-item',
  templateUrl: './edit-item-page.component.html',
  styleUrls: ['./edit-item-page.component.scss']
})
export class EditItemPage implements OnInit, OnDestroy {
  selectedItem;
  subscription;
  editItemForm: FormGroup;

  constructor(private router: Router,
              // private toast: Toast,
              private formBuilder: FormBuilder,
              private barcodeScanner: BarcodeScanner,
              private itemSandbox: ItemsSandbox) {
  }

  ngOnInit() {
    this.subscription = this.itemSandbox.itemError$.subscribe((error: string) => {
      if (error) {
        // this.toast.show(`${error}`, '3000', 'center').subscribe(
        //     toast => {
        //       console.log(toast);
        //     }
        // );
      }
    });
    this.itemSandbox.currentItem$.subscribe((currentItem: IItem) => {
      this.editItemForm = this.formBuilder.group({
        name: [currentItem.name],
        description: [currentItem.description],
        image: [currentItem.image],
        upc: [currentItem.upc],
        cost: [currentItem.cost],
        retail: [currentItem.retail],
      });
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  scan() {
    this.selectedItem = {};
    this.barcodeScanner.scan().then((barcodeData) => {
      this.itemSandbox.getItem(barcodeData.text);
    }, (err) => {
      // this.toast.show(err, '5000', 'center').subscribe(
      //     toast => {
      //       console.log(toast);
      //     }
      // );
    });
  }

  onCancel() {
    this.router.navigateByUrl('/app/tabs/catalogs');
  }
}
